export default ".message__logo-container {\n    margin-right: 5px;\n    display: flex;\n    align-items: center;\n}\n\n.message__headline {\n    font-size: 3.5vw;\n    padding: 0px;\n}\n\n.message__disclaimer {\n    position: static;\n    font-size: 14px;\n    line-height: 20px;\n    letter-spacing: 0em;\n    text-align: left;\n}\n\n.message__disclaimer > span {\n    font-weight: 400;\n    text-decoration: none;\n    white-space: normal;\n}\n\n.message__disclaimer > span > span {\n    white-space: nowrap;\n}\n\n.message__disclaimer > span > span:only-child,\n.message__disclaimer > span > span.em {\n    text-decoration: underline;\n}\n\n.message__logo:nth-of-type(1) {\n    width: 27px;\n    display: inline-block;\n    margin-right: 10px;\n    margin-left: 5px;\n}\n\n.message__logo:nth-of-type(2) {\n    width: 89px;\n    display: inline-block;\n}\n\n.message__headline strong::after {\n    content: '.';\n    color: white;\n    font-style: normal;\n    font-weight: normal;\n}\n\n.message__headline > span:nth-child(2) > strong {\n    font-weight: 500;\n}\n\n.message__headline > span:nth-child(3) > strong {\n    font-weight: 500;\n}\n\n.message__disclaimer > .tag--extra > span:only-child {\n    text-decoration: none;\n    line-height: 1.5;\n}\n\n.message__promo-container {\n    padding-right: 3px;\n}\n";