export default "button:focus .message__content,\nbutton:focus .message__content span.br {\n    text-decoration: underline;\n}\n\n.message {\n    display: block;\n    width: 100%;\n    color: #2c2e2f;\n    cursor: pointer;\n}\n\n.message__container {\n    min-width: 100%;\n}\n\n.message__content {\n    display: inline-flex;\n    align-items: center;\n    min-width: 100%;\n}\n\n.message__logo-container {\n    flex: 0 0 auto;\n    width: 70px;\n    margin-right: 0.8rem;\n}\n\n.message__messaging {\n    white-space: normal;\n    position: relative;\n    top: -0.05em;\n    line-height: 1.3;\n}\n\n.message__messaging span.br {\n    white-space: nowrap;\n}\n\n.message__promo-container {\n    display: inline;\n}\n\n.message__headline {\n    display: inline;\n    font-weight: 400;\n    font-size: inherit;\n}\n\n.message__sub-headline {\n    display: none;\n    font-size: inherit;\n}\n\n.message__disclaimer {\n    display: inline;\n    white-space: nowrap;\n    font-weight: 400;\n    font-size: inherit;\n}\n\n.message__disclaimer > span {\n    color: #0070ba;\n    font-weight: 500;\n}\n\n.message__disclaimer > span:not(.multi),\n.message__disclaimer > span.multi:last-of-type {\n    text-decoration: underline;\n}\n\n/* For non-US NI disclaimer */\n.message__disclaimer > span.multi:first-of-type {\n    color: #2c2e2f;\n    font-weight: normal;\n    text-decoration: none;\n}\n";