export default "/* Sizing will be handled by the iframe container */\nhtml,\nbody,\nbutton {\n    height: 100%;\n}\n\n.message {\n    cursor: pointer;\n}\n\n.message .br {\n    display: inline-block;\n}\n\n.message .em {\n    font-family: Helvetica, Arial, sans-serif;\n    font-weight: 400;\n}\n\n.message__container,\n.message__foreground,\n.message__content,\n.message__background {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    overflow: hidden;\n}\n\nbutton:focus .message__content,\nbutton:focus .message__content span.br {\n    text-decoration: underline;\n}\n\n.message__headline,\n.message__sub-headline,\n.message__disclaimer {\n    font-family: Helvetica, Arial, sans-serif;\n    font-weight: 300;\n}\n\n.message__headline {\n    font-family: Helvetica, Arial, sans-serif;\n    font-weight: 400;\n}\n.message__headline .weak {\n    font-family: Helvetica, Arial, sans-serif;\n    font-weight: 300;\n}\n\n.message__disclaimer > span:not(.multi),\n.message__disclaimer > span.multi:last-of-type {\n    font-weight: 300;\n    text-decoration: underline;\n    white-space: nowrap;\n}\n\n.message__background {\n    z-index: -1;\n}\n\n.message__messaging {\n    font-size: 0;\n}\n";